import 'ckeditor5/ckeditor5.css';
import { CKEditor, CKEditorContext } from '@ckeditor/ckeditor5-react';
import {
  ClassicEditor, Context, Bold, Essentials, Italic, Paragraph, ContextWatchdog, CodeBlock, AutoLink, Link, List, SimpleUploadAdapter,
  Image, ImageCaption, ImageResize, ImageStyle, ImageToolbar, LinkImage, Table, TableToolbar, HtmlEmbed, GeneralHtmlSupport, FullPage, Highlight, Heading,
  Font, FontFamily, FindAndReplace, Code, Strikethrough, Subscript, Superscript, Underline, Indent, IndentBlock, BlockQuote, HorizontalLine, ImageInsert,
  AutoImage, Markdown, Style, FontColor, FontSize, TextPartLanguage, Alignment, Mention, MediaEmbed, HtmlComment, RemoveFormat,SourceEditing
} from 'ckeditor5';
import React, { useState, useEffect, useRef } from 'react'; 
import { emailTemplateAction } from '../../../../core/resource/actions';
import { useDispatch } from 'react-redux'




const Customckediter = (props) => {
    const editorRef = useRef('');
    const dispatch = useDispatch();
    const {editorData,setEditorData,dropdownValue,setDropdownValue,bodydata} = props

    useEffect(() => {
        handleDropdownChanges(dropdownValue);

        if (bodydata !== undefined) {
            handleUpdateBody(bodydata);
        }
    }, [dropdownValue, bodydata]);

    const handleUpdateBody = (selectedValue) => {
        const editor = editorRef.current.editor;
        const currentData = editor?.getData();
        const newData = currentData + selectedValue?.replaceAll('&lt;', '<');
        editor?.setData(newData);
    };

    // const handleDropdownChanges = (event) => {
    //     const editor = editorRef.current.editor;
    //     const selectedValue = event
    //     const currentData = editor?.getData();
    //     console.log("sdfsdfdsfsdfsdfsdfsdf",currentData)
    //     const newData = currentData?.replace(/<\/p>/, ` ${selectedValue}</p>`);
    //     editor?.setData(newData);
    // };

    const handleDropdownChanges = (selectedOption) => {
        const editor = editorRef.current.editor;
        const selectedValue = selectedOption?.replaceAll('&lt;', '<');

        editor?.model.change(writer => {
            const selection = editor.model.document.selection;
            const position = selection.getFirstPosition();
            writer.insertText(selectedValue, position);
        });
    };

   const handleEditorChange = (event, editor,save) => {
    const currentData = editor.getData();
    const previousData = editorRef?.current?.previousData; 

    if (!previousData || previousData === currentData) {
        setEditorData(currentData); 
        editorRef.current.previousData = currentData
        return;
    }
    const previousImages = [...(previousData?.matchAll(/<img.*?src="(.*?)"/g) || [])].map(match => match[1]);
    const currentImages = [...(currentData?.matchAll(/<img.*?src="(.*?)"/g) || [])].map(match => match[1]);
    
    const removedImages = previousImages.filter(src => !currentImages.includes(src));

    
    removedImages.forEach(imageUrl => dispatch(emailTemplateAction.RemoveImageRequest({url:imageUrl})));
    editorRef.current.previousData = currentData;
    setEditorData(currentData);
};

    return (
        <>
        <CKEditorContext context={Context} contextWatchdog={ContextWatchdog}>
      <CKEditor
        editor={ClassicEditor}
        data={editorData}
        config={{
          plugins: [
            Essentials, Bold, Italic, Paragraph, CodeBlock, Link, AutoLink, List, Table, TableToolbar,
            Image, ImageToolbar, ImageCaption, ImageStyle, ImageResize, LinkImage, SimpleUploadAdapter,
            HtmlEmbed, GeneralHtmlSupport, FullPage, Highlight, Heading, Font, FontFamily, FindAndReplace,
            Code, Strikethrough, Subscript, Superscript, Underline, Indent, IndentBlock, BlockQuote, HorizontalLine,
            ImageInsert, AutoImage, Markdown, Style, FontColor, FontSize, TextPartLanguage, Alignment, Mention, MediaEmbed, HtmlComment, RemoveFormat,SourceEditing
          ],
          toolbar: {
            items: [
              'undo', 'redo', 'heading', '|', 'bold', 'italic', 'Alignment', '|',
              'link', 'bulletedList', 'numberedList', 'insertImage', 'image', 'simpleUploadAdapter', 'insertTable','sourceEditing', 'fullPage',
              'highlight', 'font', 'fontColor', 'style', 'textPartLanguage', '|','fontSize', 'fontFamily', '|', 'findAndReplace', 'underline', 'strikethrough', 'code', 'subscript', 'superscript', 'indent', 'outdent', 'blockQuote',
              'horizontalLine', 'image', 'AutoImage', 'Markdown','codeBlock', 'mention', 'mediaEmbed', 'htmlComment', 'removeFormat', 'linkImage','htmlEmbed'
            ],
            shouldNotGroupWhenFull: true,
            
          },
          style: {
            definitions: [
                {
                  name: 'Article Category',
                  element: 'h3',
                  classes: [ 'category' ]
                },
                {
                  name: 'Info Box',
                  element: 'p',
                  classes: [ 'info-box' ]
                },
                {
                  name: 'Quotation',
                  element: 'blockquote',
                  classes: [ 'quote' ]
                },
                {
                  name: 'Code Snippet',
                  element: 'code',
                  classes: [ 'code-snippet' ]
                },
                {
                  name: 'Warning Box',
                  element: 'div',
                  classes: [ 'warning-box' ]
                },
                {
                  name: 'Highlight',
                  element: 'span',
                  classes: [ 'highlight' ]
                },
              ]
        },
        simpleUpload: {
            uploadUrl: 'http://localhost:3001/v1/api/add-template-image',
            withCredentials: true,
            headers: {
                'X-CSRF-TOKEN': 'CSRF-Token',
                Authorization: 'Bearer <JSON Web Token>'
            }
        },
        image: {
            resizeOptions: [
            {
                name: 'resizeImage:original',
                value: null,
                icon: 'original'
            },
            {
                name: 'resizeImage:custom',
                value: 'custom',
                icon: 'custom'
            },
            {
                name: 'resizeImage:50',
                value: '50',
                icon: 'medium'
            },
            {
                name: 'resizeImage:75',
                value: '75',
                icon: 'large'
            }
        ],
        toolbar: [
            'toggleImageCaption',
            'imageTextAlternative',
            'ckboxImageEdit',
            'resizeImage:50',
            'resizeImage:75',
            'resizeImage:original',
            'resizeImage:custom',
        ] },
        htmlSupport: {
            allow: [
                {
                    attributes: true,
                    classes: true,
                    styles: true
                }
            ]
        },
        htmlEmbed: {
            showPreviews: true,
          }
        }}
        onReady={(editor) => {
            editorRef.current = {editor}}}
            onChange={(event, editor)=>handleEditorChange(event, editor,'save')}
      />
    </CKEditorContext>
        </>
    )

}

export default Customckediter;
