import React, { useState, useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { CustomButton } from '../../../globalComponents';
import { emailTemplateAction, notificationActions } from '../../../../core/resource/actions';
import { toast } from 'react-toastify';
import { addActionSchema } from '../../../../utils/ValidationSchema';
import * as yup from 'yup';
import { validateSchema } from '../../../../utils/ValidationHelper';
import CustomError from '../../../globalComponents/CustomError';
import { notify } from '../../../globalComponents/ToastNotification';

import Customckediter from './Customckediter'
import { pagination } from '../../../../utils/Constants.config'

const AddActionEmailTemplate = (props) => {
    const editorRef = useRef(null);

    const {TemplateList, openAction,getAllAction, toggleOpenAction, getAllEmailTemplalte, setSelectedActionData, selectedActionData, onHandleViewModals } = props
    const [errors, setErrors] = useState({});
    const dispatch = useDispatch();
    const {addActionTemplatePending,
        addActionTemplateRes,
        addActionTemplateError, GetActionTypeTemplatePending,
        GetActionTypeTemplateRes,
        GetActionTypeTemplateError } = useSelector(state => state.emailtemplate);
    const [isActive, setIsActive] = useState(false);
    const [editorData, setEditorData] = useState('')
    const [templateId, setTemplateId] = useState('')
    const [actionType, setActionType] = useState([])
    const [currentPage, setCurrentPage] = useState(1)

    const [addEmailTemplate, setAddEmailTemplate] = useState({
        template_name: '',
        email_title: '',
        email_type: '',
        body: '',
        is_active: '',
        remark:''
    })

    const [addAction, setAddAction] = useState({
        action_name:'',
        action_type:'',
        template_id:'',
    })
    const [Alltemplate,setAlltemplate] = useState(TemplateList?.data)

    useEffect(() => {
        setAlltemplate(TemplateList?.data)
      }, [TemplateList])

      useEffect(() => {
        getAllActionType()
      }, [currentPage])
    
      const getAllActionType = () => {
        const params = {
            page: currentPage,
            limit: pagination.limit,
          }
        dispatch(emailTemplateAction.GetActionTypeTemplateRequest(params))
      }

      useEffect(() => {
        if (!GetActionTypeTemplatePending) {
            if (GetActionTypeTemplateError) {
                setActionType([])
                return;
            }
            if (GetActionTypeTemplateRes) {
                if (GetActionTypeTemplateRes?.data?.code == 200) {
                    setActionType(GetActionTypeTemplateRes?.data?.data?.data?.data)
                }
            }
        }
    }, [
        GetActionTypeTemplatePending,
        GetActionTypeTemplateRes,
        GetActionTypeTemplateError
    ]);

    useEffect(() => {
        setAddAction({
            action_name: selectedActionData?.action_name || '',
            action_type: selectedActionData?.action_type || '',
            template_id: selectedActionData.template_id || '',
        })
        setIsActive(selectedActionData?.is_active)
        setTemplateId(selectedActionData?.id)
    }, [selectedActionData])

    useEffect(() => {
        if (!addActionTemplatePending) {
            if (addActionTemplateError) {
                toast.error(addActionTemplateError?.errorMessage)
                return;
            }
            if (addActionTemplateRes) {
                if (addActionTemplateRes?.data?.code === 200) {
                    toast.success(addActionTemplateRes?.data?.msg)
                    toggleOpenAction()
                    getAllAction()
                    dispatch(emailTemplateAction.handleClearEmailTemplateStore("HANDLE_CLEAR_ADD_ACTION_TEMPLATE_STORE"))
                }
            }
        }
    }, [
        addActionTemplateRes,
        addActionTemplateError,
        addActionTemplatePending]);

    const handleChange = (event) => {
        errors && setErrors({});
        const { name, value } = event.target
        setAddAction({ ...addAction, [name]: value })
    }

    const createAction = async () => {
        addAction.is_active = isActive
        const validationResponse = await validateSchema(addActionSchema, addAction);
        if (validationResponse.isValid) {
            setErrors({});
            if (selectedActionData) {
                addAction['_id'] = templateId
                dispatch(emailTemplateAction.UpdataActionTemplateRequest(addAction));
                getAllAction()
                toggleOpenAction()
            } else {
                dispatch(emailTemplateAction.AddActionTemplateRequest(addAction));
                getAllAction()
            }

        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        if (openAction == false) {
            resetTempateData()
        }
    }, [openAction])


    const resetTempateData = () => {
        setAddAction({
        action_name:'',
        action_type:'',
        template_id:'',
        })
        setSelectedActionData('')
        setErrors({});
        setEditorData('')
    }


    return (
        <>
            <div className={`offcanvas offcanvas-end creatNewTicketOff ${openAction ? 'show' : ''}`}

                tabIndex={-1}
                style={{
                    visibility: openAction ? 'visible' : 'hidden',
                }}
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{onHandleViewModals ? "View" : selectedActionData ? "Update" : "Create New"} Action</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => toggleOpenAction('view')}
                    />
                </div>
                <div className="offcanvas-body">
                    <div className="formPanel">
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Action Name<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <input type="text" className="form-control fs-7"
                                        placeholder="Enter Action Name"
                                        name='action_name'
                                        value={addAction?.action_name || ''}
                                        onChange={(e) => handleChange(e)}
                                    />
                                    {errors?.template_name && (
                                        <CustomError text={errors.template_name} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Action Type<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <select className="form-select" required
                                        name='action_type'
                                        value={addAction?.action_type}
                                        onChange={(e) => handleChange(e)}
                                        disabled={onHandleViewModals != '' ? true : false}
                                    >
                                        <option value='' hidden selected>Select Required Action Type</option>
                                        {actionType && actionType?.map(data => (
                                            <option value={data?.action_name}>{data?.action_name}</option>
                                        ))}
                                    </select>
                                    {errors?.email_title && (
                                        <CustomError text={errors.action_type} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Select Email Template<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <select className="form-select" required
                                        name='template_id'
                                        value={addAction?.template_id}
                                        onChange={(e) => handleChange(e)}
                                        disabled={onHandleViewModals != '' ? true : false}
                                    >
                                        <option value='' hidden selected>Select Required Email Type</option>
                                        {Alltemplate && Alltemplate?.map(data => (
                                            <option value={data?.id}>{data?.template_name}</option>
                                        ))}
                                    </select>
                                    {errors?.email_type && (
                                        <CustomError text={errors.template_id} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Is Active</label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <input type="checkbox" placeholder="Is Active"
                                        checked={isActive}
                                        onChange={(e) => setIsActive(!isActive)}
                                        disabled={onHandleViewModals != '' ? true : false}
                                    />
                                </div>
                                {errors?.is_active && (
                                    <CustomError text={errors.is_active} />
                                )}
                            </div>

                        </div>
                        <div className="d-flex align-items-center justify-content-end gap-2">
                            {onHandleViewModals == '' &&
                                <CustomButton
                                    type="button"
                                    classNames={"btn btn-primary gradientBg"}
                                    handleClick={e => createAction()}
                                    // isLoading={departmentData?updateDepartmentPending:addDepartmentPending}
                                    text={`${selectedActionData ? "Update" : "Create"} Action`}
                                />}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default AddActionEmailTemplate
